.ev-picture-upload {
  padding: 6px 15px;
  color: #e5d8ca;
  border: 2px dotted #f4ebe6;
  background-image: url('../images/upload/picture.png');
  background-repeat: no-repeat;
  background-position: top right;
  text-align: center;
  position: relative;
  min-width: 250px;
  height: 116px;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .ev-picture-upload {
    background-image: url('../images/upload/picture_2x.png');
    background-size: 65px 50px;
  }
}
.ev-picture-upload .ev-picture-upload-label {
  font-size: 24px;
  line-height: 1.1;
  margin: 10px 0;
}
.ev-picture-upload button {
  background-color: #f4ebe6;
}
.ev-picture-upload table td {
  vertical-align: top;
}
.ev-picture-upload .ev-picture-uploading > * {
  padding-bottom: 21px;
  margin-bottom: 0;
}
.ev-picture-upload .ev-picture-uploading p {
  padding: 8px;
  text-align: center;
}
.ev-picture-upload.dz-drag-hover {
  border: 2px dotted #a5907c;
}
ev-picture-button-upload * {
  display: inline;
}
